import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Link from '@mui/material/Link';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Language from '@mui/icons-material/Language';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import {height} from "@mui/system";
import {useState} from "react";

function Copyright(props) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Copyright © '}
      <Link color="inherit" href="https://vk.com/damir">
        Damir Sattarov
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const theme = createTheme();

const tiempos = ['Perfecto', 'Indefinido', 'Imperfecto']
const vocabulary = ['hablar', 'tomar', 'correr', 'vivir']
const personas = ['Yo', 'Tu', 'El', 'Nosotros', 'Vosotros', 'Ellos']

function findHint(tiempo, verbo, persona) {
  if (tiempo === 'Indefinido') {
    return indefinido(verbo, persona)
  } else if (tiempo === 'Imperfecto') {
    return imperfecto(verbo, persona)
  } else if (tiempo === 'Perfecto') {
    return perfecto(verbo, persona)
  }
}

function indefinido(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['é', 'aste', 'ó', 'amos', 'asteis', 'aron']
    return base + ends[persona]
  } else {
    const ends = ['í', 'iste', 'ió', 'imos', 'isteis', 'ieron']
    return base + ends[persona]
  }
}

function imperfecto(verbo, persona) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    const ends = ['aba', 'abas', 'aba', 'ábamos', 'abais', 'aban']
    return base + ends[persona]
  } else {
    const ends = ['ía', 'ías', 'ía', 'íamos', 'íais', 'ían']
    return base + ends[persona]
  }

}

function perfecto(verbo, persona) {
  const haber = ['he', 'has', 'ha', 'hemos', 'habéis', 'han']
  return haber[persona] + ' ' + participio(verbo)

}

function participio(verbo) {
  const base = verbo.slice(0, -2)
  if (verbo.endsWith('ar')) {
    return base + 'ado'
  } else {
    return base + 'ido'
  }
}

function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

function nextState() {
  return {
    "tiempo": tiempos[getRandomInt(tiempos.length)],
    "verbo": vocabulary[getRandomInt(vocabulary.length)],
    "persona": getRandomInt(personas.length),
  }
}

function capitalize(string) {
  return string.charAt(0).toUpperCase() + string.slice(1);
}


export default function SignIn() {

  const [state, setState] = useState(nextState())
  const [hint, setHint] = useState('')

  function findHintFor(state) {
    return findHint(state.tiempo, state.verbo, state.persona)
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline/>
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Grid container spacing={2}>
            <Grid container justifyContent="center" item xs={12}>
              <Language fontSize="large" color="secondary"/>
              <Typography component="h1" variant="h5">
                Conjugador
              </Typography>
            </Grid>
            <Grid item xs={12}>
              <TextField id="tiempo-text" fullWidth label="Tiempo" value={state.tiempo} InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid item xs={5}>
              <TextField id="persona-text" label="Persona" value={personas[state.persona]}
                         InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid item xs={7}>
              <TextField id="verbo-text" label="Verbo" value={capitalize(state.verbo)} InputProps={{readOnly: true,}}/>
            </Grid>
            <Grid container item xs={12}>
              <Typography variant="h6">Hint: {capitalize(hint)}</Typography>
            </Grid>
            <Grid item xs={5}>
              <Button type="submit" fullWidth variant="contained"
                      onClick={() => setHint(findHintFor(state))}>Hint</Button>
            </Grid>
            <Grid item xs={7}>
              <Button type="submit" fullWidth variant="contained"
                      onClick={() => {
                        setHint('')
                        setState(nextState())
                      }}>Next</Button>
            </Grid>
          </Grid>
        </Box>
        <Copyright sx={{mt: 4, mb: 4}}/>
      </Container>
    </ThemeProvider>
  );
}